/**axios
 * 
 */
import axios from 'axios';
import store from '../store/index'

console.log(process.env.NODE_ENV);
// 
if (process.env.NODE_ENV === 'development') {
    axios.defaults.baseURL = 'http://127.0.0.1:5001/api';
} else if (process.env.NODE_ENV === 'debug') {
    axios.defaults.baseURL = 'http://127.0.0.1:5001/api';
} else if (process.env.NODE_ENV === 'production') {
    axios.defaults.baseURL = 'http://www.wanyeel.com:5001/api';
}

// 
axios.defaults.timeout = 10000;

// post
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// 
axios.interceptors.request.use(
    config => {
        // tokenhttpheadertoken
        // tokentoken
        const token = store.state.token;
        token && (config.headers.access_token = token);
        return config;
    },
    error => {
        return Promise.error(error);
    }
)

// 
axios.interceptors.response.use(
    response => {
        if (response.status === 200) {
            return Promise.resolve(response);
        } else {
            return Promise.reject(response);
        }
    },
    // 200
    error => {
        console.log(error);
        if (error.response.status) {
            switch (error.response.status) {
                // 401: 
                // 
                // 
                case 401:
                    this.$router.replace({
                        path: '/login',
                        query: { redirect: this.$router.currentRoute.fullPath }
                    });
                    break;
                // 403 token
                // 
                // tokenvuextoken
                // 
                case 403:
                    // token
                    localStorage.removeItem('token');
                    store.commit('setToken', null);
                    // fullPath
                    setTimeout(() => {
                        this.$router.replace({
                            path: '/login',
                            query: {
                                redirect: this.$router.currentRoute.fullPath
                            }
                        });
                    }, 1000);
                    break;
                // 404
                case 404:

                    break;
                // 
                default:

                    break;
            }
            return Promise.reject(error.response);
        }
    }
);
/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: params
        })
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err)
            })
    });
}
/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params,timeout) {
    return new Promise((resolve, reject) => {
        if (timeout == null) {
            axios.post(url, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err)
            })
        }else{
            axios.post(url, params,timeout)
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err)
            })
        }
    });
}

